<template>
  <li class="flex flex-col mb-2 overflow-visible">
    <header class="flex flex-row space-x-1 items-center">

      <!--Button-->
      <button class="block text-left p-1 pl-0 flex flex-row items-center group text-indigo-600" @click="toggle">
        <!--Rotating Icon-->
        <rotating-arrow :toggle="state.open" />
        <div class="flex flex-row space-x-1 items-center group-hover:text-indigo-400 animate-fast">
          <span>{{ title }}</span><span class="font-light text-sm">({{ selectedFilters.length }}/{{ filters.length
          }})</span>
        </div>
      </button>

      <!--gap-->
      <div class="grow"></div>

      <!--Deselect All Button-->
      <button @click="deselect_all" class="p-1 font-thin hover:font-light text-indigo-500">
        Clear
      </button>
      <span class="text-indigo-600 font-thin">|</span>
      <!--Select All Button-->
      <button @click="select_all" class="p-1 font-thin hover:font-light text-indigo-500">
        Fill
      </button>
    </header>

    <!--Filters-->
    <div class="w-full animate-default overflow-hidden pl-2" :class="state.open ? 'max-h-[500px]' : 'max-h-0'">
      <ul v-show="state.render" class="space-y-2">
        <li v-for="filter in filters" :key="filter">
          <!--Individual Filter-->
          <checkbox-vue filter class="ml-2" :label="filter.label" :description="filter.description"
            :requires="dependency(filter.requires, global.configs)" v-model="filter.value" />
        </li>
      </ul>
    </div>
  </li>
</template>

<!---------------------------------------------------------------->

<script>
import CheckboxVue from '@/components/inputs/CheckboxVue'
import RotatingArrow from '@/components/miscellaneous/RotatingArrow'
import { inject, reactive, computed } from 'vue'

export default {
  name: "FilterDropdown",
  components: { CheckboxVue, RotatingArrow },

  props: {
    title: String, // title of the section
    filters: Array // list of filters being used
  },

  setup(props) {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')

    const state = reactive({
      open: false,
      render: false
    })

    const selectedFilters = computed(() => props.filters.filter((config) => config.value))

    function selectAll() {
      props.filters.map((config) => config.value = true)
    }

    function deselectAll() {
      props.filters.map((config) => config.value = false)
    }

    return {
      state,
      toggle,
      selectAll,
      deselectAll,
      selectedFilters
    }
  }
}
</script>