<template>
  <!--Table with results-->
  <div
    class="bg-indigo-200 rounded-lg w-fit h-fit overflow-x-auto scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100">
    <table class="border-separate">
      <thead>
        <tr>
          <template v-for="field in fields" :key='field'>
            <th v-if="field !== ' '" scope="col" class="p-2 border-l-2">
              {{ field }}
            </th>
            <th v-else class="font-light italic text-sm">
              {{ retailerType }}
            </th>
          </template>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in tableData" :key="item">
          <td v-for=" field in fields" :key="field" class="px-2 text-center"
            :class="[themes[item[' ']], themes[field]]">
            {{ item[field] }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { reactive, computed, inject } from 'vue'

export default {
  name: 'ResultsTable',

  setup() {
    /**
     * IMPORTS
     */
    const retailerType = computed(() => inject('retailer type'))
    const valuesOnline = computed(() => inject('local values online'))
    const valuesInperson = computed(() => inject('local values in-person'))
    const findLocalConfigByName = inject('find local config by name')

    const themes = reactive({
      'initial': 'bg-white bg-opacity-50 border-b-2 border-white border-opacity-50',
      'post policies': 'bg-white bg-opacity-50 border-b-2 border-white border-opacity-50',
      'difference': 'py-2 border-t-2 border-indigo-200',
      ' ': 'font-bold text-start',
    })

    const filters = computed(() => {

      var configs = {}
      if (retailerType.value === 'online')
        configs = { ...valuesOnline.value }
      else
        configs = { ...valuesInperson.value }

      return {
        'main': configs['main'].map((config) => {
          return { 'label': config.label, 'value': true, 'description': config.description, 'requires': '' }
        }),
        'fraud breakdown': configs['fraud'].map((config) => {
          return { 'label': config.label, 'value': false, 'description': config.description, 'requires': '' }
        })
      }
    })

    const fields = computed(() => {
      var list = [' ']

      for (var i in filters.value) {
        list = list.concat(filters.value[i].map((config) => {
          if (config.value)
            return config.label
        }))
      }

      return list.filter((e) => {
        return (e !== undefined)
      })
    })

    const inpersonTable = computed(() => {
      var table = {}

      for (var key in fields.value) {
        let field = fields.value[key]

        switch (field) {
          case 'fraudulent returns rate':
            table[field] = {
              'initial': 0,
              'post policies': 0
            }
            break
          case ' ':
            break
          default: {
            let config = findLocalConfigByName(field, valuesInperson.value.main)

            table[field] = {
              'initial': config['initial value'],
              'post policies': config['modelled value']
            }
          }
        }
      }

      return table
    })

    const onlineTable = computed(() => {
      var table = {}

      for (var key in fields.value) {
        let field = fields.value[key]
        switch (field) {
          case 'fraudulent returns rate':
            table[field] = {
              'initial': 0,
              'post policies': 0
            }
            break
          case ' ':
            break
          default: {
            let config = findLocalConfigByName(field, valuesOnline.value.main)

            table[field] = {
              'initial': config['initial value'],
              'post policies': config['modelled value']
            }
          }
        }
      }

      return table
    })

    const tableData = computed(() => {
      var rows = []

      var data
      if (retailerType.value === 'online')
        data = onlineTable.value
      else
        data = inpersonTable.value

      for (var key in data) {
        rows.map((row) => {
          row[key] = data[key][row[' ']]
        })
      }

      return rows
    })

    return {
      themes,
      fields,
      tableData,
      retailerType
    }
  }
}
</script>