<template>
	<!-- Various global.configs -->
	<form class=" overflow-y-auto h-full scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100"
		@input="validate" v-on:submit.prevent>
		<!--Retailer Information Section-->
		<section-wrapper title="Information">
			<div class="flex place-content-center font-light w-full">
				<div class="space-y-6 py-6">
					<span class="flex flex-row">I am an
						<select-component :options="retailerSelect.types" v-model="retailerSelect.value"
							:descript="retailerSelect.description" text />retailer.
					</span>
					<span class="flex flex-row">
						I sell
						<select-component :options="productSelect.types" v-model="productSelect.value"
							:description="productSelect.description" text />
						.
					</span>
				</div>
			</div>
		</section-wrapper>
		<!--Initial Values Section-->
		<section-wrapper title="Initial Values">
			<div class="flex flex-wrap gap-2 place-content-center font-bold py-6">
				<div class="text-center max-w-lg min-w-fit flex-1" v-show="retailerType !== 'in-person'">
					Online :
					<div class="border-4 mt-2 border-slate-200 bg-white rounded-lg p-4 w-full h-fit space-y-4 font-normal">
						<div class="flex flex-col gap-4">
							<div class="flex flex-grow" v-for="config in valuesOnline.main" :key="config">
								<input-vue class="flex-1" :config="config" v-model="config['initial value']" />
							</div>
						</div>
						<!--Online Fraud Breakdown Dropdown-->
						<dropdown-container title="fraud breakdown">
							<div class="flex flex-col gap-4">
								<input-vue v-for="config in valuesOnline.fraud" :key="config" :config="config"
									v-model="config['initial value']" />
							</div>
						</dropdown-container>
					</div>
				</div>
				<div class="text-center max-w-lg min-w-fit flex-1" v-show="retailerType !== 'online'">
					In-person :
					<div class="border-4 mt-2 border-slate-200 bg-white rounded-lg p-4 w-full h-fit space-y-4 font-normal">
						<div class="flex flex-col gap-4">
							<div class="flex flex-grow" v-for="config in valuesInperson.main" :key="config">
								<input-vue class="flex-1" :config="config" v-model="config['initial value']" />
							</div>
						</div>
						<!--Inperson Fraud Breakdown Dropdown-->
						<dropdown-container title="fraud breakdown">
							<div class="flex flex-col gap-4">
								<input-vue v-for="config in valuesInperson.fraud" :key="config" :config="config"
									v-model="config['initial value']" />
							</div>
						</dropdown-container>
					</div>
				</div>
			</div>
		</section-wrapper>
	</form>
</template>

<!---------------------------------------------------------------->

<script>
import InputVue from '@/components/inputs/InputVue'
import SectionWrapper from '@/components/containers/SectionWrapper'
import SelectComponent from '@/components/inputs/SelectComponent'
import DropdownContainer from '@/components/containers/DropdownContainer'

import { watch, reactive, inject, computed } from 'vue'

export default {
	name: 'ConfigurationTab',
	components: {
		InputVue,
		SectionWrapper,
		SelectComponent,
		DropdownContainer
	},

	setup() {
		/**
		 * IMPORTS
		 */
		const presetValues = inject('preset values')
		const findConfigByName = inject('find config by name')
		const retailerSelect = inject('retailer select')
		const valuesOnline = inject('values online')
		const valuesInperson = inject('values in-person')

		const productSelect = reactive({
			value: 'women\'s clothing',
			types: [
				'women\'s clothing',
				'men\'s clothing',
				'children\'s clothing',
				'footwear',
				'other',
			],
			description: 'only used as preset values'
		})
		const productType = computed({
			get: () => productSelect.value
		})
		const retailerType = computed({
			get: () => retailerSelect.value
		})

		watch(productType, (value) => {
			const presets = presetValues[value]

			for (var key in presets)
				findConfigByName(key).value = presets[key]
		})

		return {
			retailerSelect,
			retailerType,
			productSelect,
			valuesOnline,
			valuesInperson
		}
	}
};
</script>