              <checkbox-vue :config="config" v-model="config.value" :local="policies" />
            </li>
          </ul>
        </dropdown-container>

        <!--Return Policies and Requirements Dropdown-->
        <dropdown-container title="Return Policies and Requirements">
          <ul class="space-y-2 w-full">
            <li v-for="config in returnPoliciesAndRequirements" :key="config">
              <checkbox-vue :config="config" v-model="config.value" :local="policies" />
