                </svg>
                Clear all
              </button>
            </ul>
            <!--If No Policy is Selected-->
            <div v-else>
              None selected
            </div>
          </div>
        </div>
      </div>
    </section-wrapper>

    <div class="grow"></div>


    <!-- End buttons -->
    <div class="sticky bottom-0 animate-default border-slate-200 border-t-2"
      :class="state.open ? 'max-h-[50%]' : 'max-h-[0px]'">
      <div class="flex flex-row relative z-20 bg-white w-full items-end animate-default overflow-visible">
        <div class="absolute top-[-25px] grid place-items-center w-full">
          <button @click="toggle(state)"
            class="h-[25px] shadow-sm rounded-t-xl z-30 px-2 w-fit mr-0 animate-default origin-bottom"
            :class="state.open ? 'bg-indigo-600 text-indigo-100 shadow-md scale-y-[-100%]' : 'shadow-xs hover:shadow-md bg-slate-200 text-slate-600 hover:bg-indigo-200 hover:text-slate-800'">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 scale-y-50 rotate-180" fill="none"
              viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
              <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
            </svg>
          </button>
        </div>
        <div
          class="w-full overflow-x-scroll scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100">
          <results-component v-show="state.render" class="mt-2 mx-2" />
        </div>
      </div>
    </div>
  </form>
</template>

