  name: 'PoliciesPartition',
  components: {
    DropdownContainer,
    CheckboxVue,
    SectionWrapper,
    ResultsComponent,
    CopyButton,
    DownloadButton
  },

  setup() {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')
    const dependency = inject('dependency')

    const state = reactive({
      open: false,
      render: false,
    })
    const configs = reactive({ ...inject('configs') })
    const policies = reactive({
      'interventions': [
        {
          label: 'Implementing Radio Frequency Identification (RFID)',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Implementing unique barcodes, or product’s Stock-Keeping Unit (SKU) number',
          value: false,
          requires: [],
          description: 'Instead of Universal Product Codes'
        },
        {
          label: 'Having the unique product ID number and information on receipts',
          value: false,
          requires: [
            {
              label: 'interventions.Implementing unique barcodes, or product’s Stock-Keeping Unit (SKU) number',
              to_be: true
            },
          ],
          description: ''
        },
        {
          label: 'Implementing security tags',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Better information system (e.g. record the product ID on receipt)',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Providing eReceipts',
          value: false,
          requires: [],
          description: 'Addresses fraudulent returns. Improves sustainability and data collection'
        },
        {
          label: 'Allowing Paypal, Klarna, Credit, or buy now and pay later',
          value: false,
          requires: [
            {
              label: 'interventions.Providing eReceipts',
              to_be: true
            },
          ],
          description: ''
        },
        {
          label: 'Allowing Apple Pay, Google Pay, or Amazon Pay',
          value: false,
          requires: [
            {
              label: 'interventions.Providing eReceipts',
              to_be: true
            },
          ],
          description: ''
        },
        {
          label: 'Improving security of online payment',
          value: false,
          requires: [],
          description: 'e.g. using security software, having a fraud prevention team, or using 3rd party tools to monitor for fake credit/debit cards'
        },
        {
          label: 'Increasing the deployment of CCTVs',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Increasing the deployment of guarding',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Regulary training returns staff in spotting fraudulent returns ',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Implementing ban lists / sending warnings to serial returners',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Establishing a dedicated team to investigate suspected fraudulent behaviours',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Using the civil recovery route',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Installing cameras in the fulfilment center',
          value: false,
          requires: [],
          description: 'As a means to prove the correct products were packed'
        },
        {
          label: 'Requiring parcels to be signed for by the customer',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Requiring police reports for reportedly stolen or loss parcels',
          value: false,
          requires: [],
          description: 'For expensive items'
        },
      ],
      'return policies and requirements': [
        {
          label: 'Shortening the return period to 14 days',
          value: false,
          requires: [],
          description: 'From 28 days'
        },
        {
          label: 'Requiring original receipts for returns',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Requiring unremoved tags for returns',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Refunding imperfect returns using gift-cards',
          value: false,
          requires: [],
          description: 'e.g. lost receipts or tags, exceeded return window'
        },
        {
          label: 'Returning funds only to the original payment method',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Requiring customers to provide identifying information when returning items',
          value: false,
          requires: [],
          description: 'e.g. providing a post code and signature for in-store returns'
        },
        {
          label: 'Requiring refunds to be handled at specifc return desks',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Stricter supervising of in-store refunds',
          value: false,
          requires: [],
          description: 'e.g. employees cannot refund their own purchased products and have to be signed by managers + random check of the refund situation'
        },
      ],
      'customer convenience': [
        {
          label: 'Providing free online deliveries',
          value: false,
          requires: [],
          description: 'or if above certain spending'
        },
        {
          label: 'Charging the return shipping fee',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Using drop off returns',
          value: false,
          requires: [],
          description: 'royal mail, drop-off points (such as sister-brand stores), or locker banks'
        },
        {
          label: 'Using a collection service',
          value: false,
          requires: [],
          description: 'e.g. Hermers'
        },
        {
          label: 'Implementing a loyalty programme for free delivery and returns',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Cooperating with 3rdparties (e.g. Rebound to offer QR code Return Portal services and technologies when returning via posting)',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Disallowing pre-paid return labels',
          value: false,
          requires: [],
          description: 'customers must contact the retailer first, or issue the return label via retailer’s online portal'
        },
        {
          label: 'Delivering items in 3 to 5 working days',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Detailed product-related information (e.g. measurements)',
          value: false,
          requires: [],
          description: 'e.g. product dimensions / measurements, model’s measurements such as height & weight'
        },
        {
          label: 'Offering an online sizing tool',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Using a rating scale for customer reviews',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Allowing pictures within customer reviews',
          value: false,
          requires: [],
          description: ''
        },
        {
          label: 'Product demonstration videos',
          value: false,
          requires: [],
          description: 'for apparel: virtual catwalks'
        },
        {
          label: 'Providing live chat for support',
          value: false,
          requires: [],
          description: ''
        },
      ]
    })

    const valuesOnline = computed(() => configs['values online'])
    const valuesInperson = computed(() => configs['values in-person'])

    const interventions = computed(() => policies['interventions'])
    const returnPoliciesAndRequirements = computed(() => policies['return policies and requirements'])
    const customerConvenience = computed(() => policies['customer convenience'])

    const selectedPolicies = computed(() => {

      var list = []
      for (var key in policies) {
        var r = (policies[key].filter((policy) => {
          return (policy.value === true)
        }))
        list = list.concat(r)
      }

      return list
    })

    const activePolicies = computed(() => {
      return selectedPolicies.value.map((policy) => {
        return this.global.model.filter((row) => {
          return row.Factors === policy.label
        })[0]
      })
    })

    function deselectAll() {
      selectedPolicies.value.map((config) => config.value = false)
    }

    function applyPercentage(value, percentage) {
      return (value * (100 + percentage) / 100)
    }

    function modelValue(config) {
      const modeller = config['modelled by'].split(/[()]/i)
      config['modelled value'] = config['initial value']

      for (var i in activePolicies) {
        let x = parseFloat(activePolicies[i][modeller[0]])
        if (isNaN(x)) x = 0

        if (modeller[1] === '%')
          config['modelled value'] = applyPercentage(config['modelled value'], x)
      }
    }

    /**
     * Updates values using modeller
     */
    function updateValues() {
      valuesOnline.value.fraud.map((config) => { modelValue(config) })
      valuesOnline.value.main.map((config) => { modelValue(config) })

      valuesInperson.value.fraud.map((config) => { modelValue(config) })
      valuesInperson.value.main.map((config) => { modelValue(config) })
    }

    provide('update values', updateValues)
    provide('local values online', valuesOnline.value)
    provide('local values in-person', valuesInperson.value)

    return {
      state,
      toggle,
      policies,
      interventions,
      returnPoliciesAndRequirements,
      customerConvenience,
      selectedPolicies,
      deselectAll,
      dependency
    }
  }
}
</script>