<template>
	<div class="relative h-full w-full overflow-hidden">
		<!--Split View-->
		<vue-split-view v-if="state.open" direction="horizontal" a-min="20%" a-max="80%">
			<template #A>
				<div class="relative h-full w-full px-2">
					<policies-partition />
				</div>
			</template>

			<template #B>
				<div class="relative h-full w-full px-2 animate-default" :class="hovered && 'blur-sm scale-[98%]'">
					<policies-partition />
				</div>
			</template>
		</vue-split-view>
		<!--Regular View-->
		<policies-partition v-else class="border-r-2 border-slate-200 pr-2 animate-default" :class="hovered && 'pr-4'" />
		<hr v-show="!state.open"
			class="absolute h-1/3 right-0 top-1/3 pointer-events-none w-2 translate-x-4 bg-slate-500 blur-md animate-default"
			:class="hovered && 'translate-x-2 bg-slate-800'" />
		<!--On and Off Button-->
		<button @click="toggle(state)" @mouseover="hovered = true" @mouseleave="hovered = false"
			class="absolute shadow-sm rounded-full p-2 flex-none animate-default"
			:class="state.open
			? 'top-0 right-0 bg-indigo-600 text-indigo-100 shadow-md scale-[-100%]'
			: 'top-1/2 right-0 translate-x-2 scale-105 shadow-xs hover:shadow-md bg-slate-200 text-slate-600 hover:translate-x-0 hover:scale-110 hover:bg-indigo-300 hover:text-slate-800'">
			<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"
				stroke-width="2">
				<path stroke-linecap="round" stroke-linejoin="round"
					:d="state.open ? 'M6 18L18 6M6 6l12 12' : 'M12 4v16m8-8H4'" />
			</svg>
		</button>
	</div>
</template>

<!---------------------------------------------------------------->

<script>
import PoliciesPartition from '@/components/PoliciesPartition'
import VueSplitView from 'vue-split-view'

import { ref, reactive, inject } from 'vue'

export default ({
	name: 'PoliciesTab',
	components: {
		PoliciesPartition,
		VueSplitView
	},

	setup() {
		/**
		 * IMPORTS
		 */
		const toggle = inject('toggle')

		const state = reactive({
			open: false,
			render: false,
		})

		const hovered = ref(false)

		return {
			state,
			hovered,
			toggle
		}
	}
})
</script>