<template>
  <div class="bg-indigo-100 rounded-2xl p-2 h-full">
    <div class="flex flex-col">
      <div class="grid grid-cols-1">
        <div class="justify-self-center relative gap-2">
          <TabsWrapper class="animate-default">
            <TabComponent title="Table">
              <results-sub-container>
                <template v-slot:graph>
                  <table-vue />
                </template>
              </results-sub-container>
            </TabComponent>
            <TabComponent title="Bar chart">
              <results-sub-container>
                <template v-slot:graph>
                  <bar-chart />
                </template>
              </results-sub-container>
            </TabComponent>
            <TabComponent title="Doughnut chart">
              <results-sub-container>
                <template v-slot:graph>
                  <doughnut-chart />
                </template>
              </results-sub-container>
            </TabComponent>
            <TabComponent title="Radar chart">
              <results-sub-container>
                <template v-slot:graph>
                  <radar-chart />
                </template>
              </results-sub-container>
            </TabComponent>
          </TabsWrapper>
        </div>
      </div>
    </div>
  </div>
