<script>
import TableVue from '@/components/visualizers/results/TableVue'
import BarChart from '@/components/visualizers/results/BarChart'
import DoughnutChart from '@/components/visualizers/results/DoughnutChart'
import RadarChart from '@/components/visualizers/results/RadarChart'
import TabComponent from '@/components/containers/TabComponent'
import TabsWrapper from '@/components/containers/TabsWrapper'
import ResultsSubContainer from './ResultsSubContainer.vue'

import { inject, reactive, computed, provide } from 'vue'

export default {
  name: 'ResultsComponent',
  components: {
    TableVue,
    BarChart,
    DoughnutChart,
    RadarChart,
    TabComponent,
    TabsWrapper,
    ResultsSubContainer
  },

  setup() {
    /**
     * IMPORTS
     */
    const retailerSelect = computed(() => inject('retailer select'))

    const displaySelect = reactive({
      type: 'online',
      options: ['online', 'in-person']
    })

    provide('retailer type', computed(() => {
      return (retailerSelect.value !== 'online and in-person') ? retailerSelect.value : displaySelect.value
    }))

    return {
      displaySelect,
    }
  }
}
</script>